.dropdown-el input:checked + label:nth-child(2) { margin-top: 0; position: relative; }

.dropdown-el::after { content: ""; position: absolute; right: 0.8rem; top: 0.9rem; transition: .4s all ease-in-out; border: 0.3rem solid #212121; border-color: #212121 transparent transparent transparent; border: 0.3rem solid var(--color); border-color: var(--color) transparent transparent transparent; }

.dropdown-el.expanded { border-radius: .25rem; padding: 0; max-height: 15rem; box-shadow: 0px 11px 15px rgba(0, 0, 0, 0.2); border: 0.06rem solid #eeeeee; background: #fff; border: 0.06rem solid var(--border-color); background: var(--bg); }

.dropdown-el.expanded label { border-top: 0.06rem solid #eeeeee; border-top: 0.06rem solid var(--border-color); }

.dropdown-el.expanded label:hover { color: #212121; color: var(--color); }

.dropdown-el.expanded input:checked + label { font-weight: bold; }

.dropdown-el.expanded::after { transform: rotate(-180deg); top: .55rem; }
